import React from 'react'
import {
    Card
  } from 'reactstrap';
import axios from 'axios'
import LogoContainer from '../components/LogoContainer'
import {CopyToClipboard} from 'react-copy-to-clipboard';


class Cards extends React.Component {
    constructor(props) {
      super(props);
  
      this.onChange = {
        name1: this.handleChange.bind(this, 'name1'),
        name2: this.handleChange.bind(this, 'name2'),
      }
  
      this.state = {
        valor1: '',
        valor2: '',
        total: ''
      };
      this.getMoney = this.getMoney.bind(this)
    };
    handleChange(name, event) {
      this.getMoney(name, event.target.value);
      this.setState({copied: ''})
    };
 
    onCopy = () => {
      this.setState({copied: 'Copiado!'});
    };

    getMoney(name,e) {
      let self = this;
      function round(value, decimals) {
        return Number(Math.round(value+'e'+decimals)+'e-'+decimals);
      }
        axios.get('https://free.currconv.com/api/v7/convert?apiKey=b4e8126aaeefdc638d79&q=EUR_ARS,USD_ARS')
          .then(function (response) {
            if(name==='name1'){
              let EURtoARS = response.data.results.EUR_ARS.val
              let totalEUR = EURtoARS*e
              self.setState({
                totalEURO: '$ '+round(totalEUR,2)
              })
              
            }else{
            let USDtoARS = response.data.results.USD_ARS.val
            let totalUSDs = USDtoARS*e
            self.setState({
              totalUSD: '$ '+round(totalUSDs,2)
            })
            }
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          });
    }  


render(){

    return (
      <div className="section">
        <div className="full-height">
          <div className="absolute-center">
            <div className="container">
              <div className="row">
          <LogoContainer />
          <Card className="col-12 mt-5 border-0 bg-dark">
              <div className="row mx-auto">
                  <div className="col-12 pt-3">
                  <div className="input-group">
                    <div className="input-group-prepend">
                        <span className="input-group-text bg-input-green" id="basic-addon">
                        <i className="fa fa-usd" aria-hidden="true"></i>
                        </span> 
                    </div>
                   <input type="tell" pattern="[0-9]*" className="form-control bg-input-green text-dark" onChange={ this.onChange.name2 }  placeholder="DOLAR"/>
                </div>
                 </div>
                  <div className="col-12 pt-3">
                  <div className="input-group">
                    <div className="input-group-prepend">
                        <span className="input-group-text  bg-input-green" id="basic-addon">
                        <i className="fa fa-euro" aria-hidden="true"></i>
                        </span> 
                    </div>
                   <input type="tell" pattern="[0-9]*" className="form-control  bg-input-green text-dark" onChange={ this.onChange.name1 }  placeholder="EURO"/>
                </div>                  
                  </div>
                  
                  <div className="col-8 mx-auto mt-5 mb-5">

                  <div className="input-group">
                    <div className="input-group-prepend bg-input-white">
                        <span className="input-group-text color-green" id="basic-addon">
                        <i className="fa fa-usd" aria-hidden="true"></i>
                        </span> 
                    </div>
                   <input type="text" className="form-control bg-dark border-ahora totalCur text-white" value={this.state.totalUSD} disabled placeholder="Total en USD"/> 
                    <CopyToClipboard
            onCopy={this.onCopy}
            text={this.state.totalUSD}>
            <button onClick={this.onClick} className="ml-1 btn btn-dark">
                <i className="fa fa-clipboard" aria-hidden="true"></i>
              </button>
          </CopyToClipboard>
                </div>   
                <div className="input-group mt-2">
          <div className="input-group-prepend bg-input-white">
                        <span className="input-group-text color-green" id="basic-addon">
                        <i className="fa fa-euro" aria-hidden="true"></i>
                        </span> 
                    </div>
                   <input type="text" className="form-control bg-dark border-ahora totalCur text-white" value={this.state.totalEURO} disabled placeholder="Total en EURO"/> 
                    <CopyToClipboard
            onCopy={this.onCopy}
            text={this.state.totalEURO}>
            <button className="ml-1 btn btn-dark">
                <i className="fa fa-clipboard" aria-hidden="true"></i>
              </button>
          </CopyToClipboard>
                </div>
                
                <p className="text-center text-white" id="copiado"> {this.state.copied} </p>  

                  </div>
            </div>
        <p className="text-muted small text-center"><i className="fa fa-copyright" aria-hidden="true"></i> Hecho con React por <a href="https://www.instagram.com/swing_av" className="text-muted"> Agencia Swing</a></p>
          </Card>
        </div>
        </div>
        </div>
        </div>
        </div>
        )
}
}
export default Cards